.Car {
    margin-bottom: 10px;
    display: block;
    padding: 10px;
    border-radius: 5px;
    -webkit-transition: border, -webkit-box-shadow .3s;
    transition: border, -webkit-box-shadow .3s;
    -o-transition: border, box-shadow .3s;
    transition: border, box-shadow .3s;
    transition: border, box-shadow .3s, -webkit-box-shadow .3s;
}

.input {}

.input:active, .input:focus {
    outline: none;
}

.input.green {
    border: 1px solid green;
}

.input.red {
    border: 1px solid red;
}

.input.bold {
    border: 2px solid green;
    font-weight: bold;
}


